import Eth from "@ledgerhq/hw-app-eth";
import Base from "./base/base";

export default class Ethereum extends Base {
    constructor(transport) {
        super(transport);
        this.bip44 = "44'/60'/0'/0/0";
    }
    // getAddress
    // data =>  {
    //   }
    // response => {
    //     address: "0x0000000000000000000000000000000000"
    //   }
    async getAddress(data) {
        console.log("eth getAddress 1");
        const { address } = await new Eth(this.transport).getAddress(this.bip44);
        console.log("eth getAddress 2");
        return { address };
    }

    // signTransaction
    // data =>  {
    //     encodeTx: "0x0000000000000000000000000000000000"
    //   }
    // response => {
    //     signature: "0x0000000000000000000000000000000000"
    //   }
    async signTransaction(data) {
        const rawTxHex = data.encodeTx;
        const { r, s, v } = await new Eth(this.transport)
            .signTransaction(this.bip44, rawTxHex);
        return { signature: { r, s, v } };
    }
}